import React from 'react';

function NewItems() {
  const items = [
    {
      name: '   ',
      image: 'path_to_image1.jpg',
    },
    {
      name: '  ',
      image: 'path_to_image2.jpg',
    },
    //   ...
  ];

  return (
    <section className="new_items">
      <h2></h2>
      <ul>
        {items.map((item, index) => (
          <li key={index}>
            <img src={item.image} alt={item.name} />
            <p>{item.name}</p>
          </li>
        ))}
      </ul>
    </section>
  );
}

export default NewItems;
